<template>
  <div class="container">
    <div>
      <h1>轮播图管理</h1>
      <h5>首页三个轮播图：</h5>
    </div>
    <template v-for="item in arr">
      <div class="lbtInput" :key="item.id">
        <el-form :label-position="labelPosition" label-width="80px" :model="item">
          <el-form-item label="pictureURL">
            <el-input type="textarea" v-model="item.picture"></el-input>
          </el-form-item>
          <el-form-item label="title">
            <el-input v-model="item.title"></el-input>
          </el-form-item>
          <el-form-item label="description">
            <el-input v-model="item.description"></el-input>
          </el-form-item>
        </el-form>
        <div class="bt">
          <el-button type="success" plain @click="send(item)">提交</el-button>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import { getlbt,updatelbt } from "../../network/data";
export default {
  created() {
    getlbt().then(res=>{
      console.log(res);
      this.arr=res
    })
  },
  data() {
    return {
      labelPosition: "right",
      arr: [

      ]
    }
  },
  methods: {
      send(item) {
        
        document.execCommand('copy');
        updatelbt(item).then(res=>{
          console.log(res);
          this.$message({
          showClose: true,
          message: '修改成功',
          type: 'success',
          duration:800
        });
          
        })
      }
    }
};
</script>

<style>
.container {
  margin-left: 20px;
}
.lbtInput {
  padding: 20px;
  width: 450px;
  border: 5px solid #ededed;
  margin-bottom: 5px;
}
.bt {
  margin-left: 180px;
}
</style>